import React, { useState, useEffect } from 'react';
import { useParams, useNavigate, Link } from 'react-router-dom';
import SEOHeadBasic from '../components/SEOHeadBasic';
import { useAuth } from '../contexts/AuthContext';
import { getProductBySlug, deleteProduct, getProductsByIds, getImageUrl } from '../lib/supabase';
import { Product } from '../lib/supabase';
import { ContextualCMS, CMSTrigger } from '../components/ContextualCMS';
import { ImageUpload } from '../components/ImageUpload';
import { RelationshipSelector } from '../components/RelationshipSelector';
import { DeleteConfirmation } from '../components/DeleteConfirmation';
import { ProductTabs, hasTabContent } from '../components/ProductTabs';
import { ProductEditForm } from '../components/ProductEditForm';
import { ProductCard } from '../components/ui/Card';
import { getRandomMusicalMessage } from '../utils/musicalLoadingMessages';


export const ProductDetail: React.FC = () => {
  const { slug } = useParams<{ slug: string }>();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [product, setProduct] = useState<Product | null>(null);
  const [pairsWellWithProducts, setPairsWellWithProducts] = useState<Product[]>([]);
  const [alsoConsiderProducts, setAlsoConsiderProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [relatedLoading, setRelatedLoading] = useState(false);
  const [isCMSOpen, setIsCMSOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [deleteLoading, setDeleteLoading] = useState(false);
  const [selectedImage, setSelectedImage] = useState<string | null>(null);
  const [isImageModalOpen, setIsImageModalOpen] = useState(false);

  useEffect(() => {
    const loadProduct = async () => {
      if (!slug) return;
      
      try {
        const productData = await getProductBySlug(slug);
        setProduct(productData);
        
        // Load cross-referenced products
        if (productData) {
          setRelatedLoading(true);
          
          // Load pairs_well_with products
          if (productData.pairs_well_with && productData.pairs_well_with.length > 0) {
            const pairsProducts = await getProductsByIds(productData.pairs_well_with);
            setPairsWellWithProducts(pairsProducts);
          }
          
          // Load also_consider products
          if (productData.also_consider && productData.also_consider.length > 0) {
            const alsoProducts = await getProductsByIds(productData.also_consider);
            setAlsoConsiderProducts(alsoProducts);
          }
          
          setRelatedLoading(false);
        }
      } catch (error) {
        console.error('Error loading product:', error);
      } finally {
        setLoading(false);
      }
    };

    loadProduct();
  }, [slug]);

  const handleEditSuccess = async () => {
    // Reload product after successful update
    if (slug) {
      const productData = await getProductBySlug(slug);
      setProduct(productData);
    }
  };

  const handleDelete = async () => {
    if (!product) return;
    
    setDeleteLoading(true);
    try {
      const { error } = await deleteProduct(product.id);
      if (error) {
        console.error('Error deleting product:', error);
        return;
      }
      
      // Redirect to products page after successful deletion
      navigate('/products');
    } catch (error) {
      console.error('Error deleting product:', error);
    } finally {
      setDeleteLoading(false);
      setIsDeleteModalOpen(false);
    }
  };

  const openImageModal = (imagePath: string) => {
    setSelectedImage(imagePath);
    setIsImageModalOpen(true);
  };

  const closeImageModal = () => {
    setIsImageModalOpen(false);
    setSelectedImage(null);
  };

  const formatPrice = (price?: number) => {
    if (!price) return null;
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(price);
  };

  const getCallToActionButton = () => {
    if (product?.available_for_demo) {
      return (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Link
            to={`/contact?product=${encodeURIComponent(product.title)}&manufacturer=${encodeURIComponent(product.manufacturer?.name || '')}&demo=home`}
            className="bg-white border-2 border-stone-900 text-stone-900 py-4 px-6 font-medium tracking-wide hover:bg-stone-900 hover:text-white transition-all duration-300 text-center shadow-sm hover:shadow-md"
          >
            <div className="flex items-center justify-center">
              <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
              </svg>
              Home Demo
            </div>
          </Link>
          <Link
            to={`/contact?product=${encodeURIComponent(product.title)}&manufacturer=${encodeURIComponent(product.manufacturer?.name || '')}&demo=store`}
            className="bg-stone-900 text-white py-4 px-6 font-medium tracking-wide hover:bg-stone-800 transition-all duration-300 text-center shadow-sm hover:shadow-md"
          >
            <div className="flex items-center justify-center">
              <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-4m-5 0H9m0 0H5m0 0h2M7 7h10M7 11h10M7 15h10" />
              </svg>
              Store Demo
            </div>
          </Link>
        </div>
      );
    }
    

    
    if (product?.local_only) {
      return (
        <button className="w-full bg-stone-600 text-white py-3 px-6 rounded-lg hover:bg-stone-700 focus:outline-none focus:ring-2 focus:ring-stone-500 focus:ring-offset-2 font-medium">
          VIEW IN SHOWROOM
        </button>
      );
    }
    
    return (
      <button className="w-full bg-accent-600 text-white py-3 px-6 rounded-lg hover:bg-accent-700 focus:outline-none focus:ring-2 focus:ring-accent-500 focus:ring-offset-2 font-medium">
        CONTACT FOR PRICING
      </button>
    );
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-warm-white py-12">
        <div className="container-custom text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-accent-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">{getRandomMusicalMessage()}</p>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="min-h-screen bg-warm-white py-12">
        <div className="container-custom text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">Product Not Found</h1>
          <p className="text-gray-600 mb-6">The product you're looking for doesn't exist.</p>
          <Link to="/products" className="btn-primary">
            Back to Products
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-warm-white">
      <SEOHeadBasic
        title={product.title}
        description={`${product.title} by ${product.manufacturer?.name || 'Premium Audio'}. ${product.brief_description || 'High-end audio equipment available at Fidelis Audio, New Hampshire.'}`}
        canonical={`/products/${product.slug}`}
      />
      
      {/* Premium Hero Section */}
      <div className="relative bg-warm-beige border-b border-stone-200 -mt-4">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-16 lg:py-24">


          {/* Admin Actions */}
          {user && (
            <div className="absolute top-8 right-8 flex space-x-3">
              <CMSTrigger
                onClick={() => setIsCMSOpen(true)}
                label="Edit Product"
                context="product"
              />
              <button
                onClick={() => setIsDeleteModalOpen(true)}
                className="bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 transition-all duration-200 text-sm font-medium"
              >
                Delete Product
              </button>
            </div>
          )}

          {/* Product Header */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 items-center">
            {/* Product Images */}
            <div className="space-y-6">
              {/* Main Image - Now sits directly on background */}
              <div className="relative group">
                {product.product_hero_image ? (
                  <div className="relative">
                    <img
                      src={getImageUrl(product.product_hero_image)}
                      alt={product.title}
                      className="w-full h-auto max-h-[600px] object-contain mix-blend-multiply"
                    />
                  </div>
                ) : (
                  <div className="w-full h-[500px] bg-gradient-to-br from-stone-100 to-stone-200 rounded-2xl flex items-center justify-center border-2 border-dashed border-stone-300">
                    <div className="text-center">
                      <svg className="h-16 w-16 text-stone-400 mx-auto mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                      </svg>
                      <p className="text-stone-500 font-medium">No image available</p>
                    </div>
                  </div>
                )}
              </div>

              {/* Thumbnail Gallery */}
              {product.product_gallery && product.product_gallery.length > 0 && (
                <div className="grid grid-cols-4 gap-4">
                  {product.product_gallery.map((imagePath, index) => (
                    <div
                      key={index}
                      className="relative group cursor-pointer overflow-hidden rounded-xl bg-warm-white shadow-md"
                      onClick={() => openImageModal(imagePath)}
                    >
                      <img
                        src={getImageUrl(imagePath)}
                        alt={`${product.title} - Image ${index + 1}`}
                        className="w-full h-24 object-cover transition-transform duration-300 group-hover:scale-110"
                      />
                      <div className="absolute inset-0 bg-black/20 opacity-0 group-hover:opacity-100 transition-opacity duration-200" />
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/* Product Information */}
            <div className="space-y-10">
              {/* Category and Manufacturer Badges - Premium Design */}
              <div className="flex flex-wrap gap-4">
                {product.categories && (
                  <Link
                    to={`/products/list?category=${product.categories.id}`}
                    className="inline-flex items-center px-6 py-3 bg-stone-900 text-white text-sm font-medium tracking-wide uppercase hover:bg-stone-800 transition-all duration-300 shadow-lg hover:shadow-xl"
                  >
                    {product.categories.name}
                  </Link>
                )}
                {product.manufacturer && (
                  <Link
                    to={`/manufacturers/${product.manufacturer.slug}`}
                    className="inline-flex items-center px-6 py-3 border-2 border-stone-900 text-stone-900 text-sm font-medium tracking-wide uppercase hover:bg-stone-900 hover:text-white transition-all duration-300"
                  >
                    {product.manufacturer.name}
                  </Link>
                )}
              </div>

              {/* Product Title - Larger and More Prominent */}
              <div>
                <h1 className="text-6xl lg:text-7xl xl:text-8xl font-extralight text-stone-900 leading-none tracking-tighter mb-6">
                  {product.title}
                </h1>
                {product.manufacturer && (
                  <p className="text-2xl lg:text-3xl text-stone-600 font-light tracking-wide">
                    by{' '}
                    <Link
                      to={`/manufacturers/${product.manufacturer.slug}`}
                      className="hover:text-stone-900 transition-colors duration-300 border-b border-stone-300 hover:border-stone-900"
                    >
                      {product.manufacturer.name}
                    </Link>
                  </p>
                )}
              </div>

              {/* Price Display */}
              {product.show_price && product.price && (
                <div className="text-4xl font-light text-stone-900">
                  {formatPrice(product.price)}
                </div>
              )}

              {/* Sophisticated Call to Action */}
              <div className="pt-6 space-y-4">
                {getCallToActionButton()}
              </div>

              {/* Product Tags */}
              <div className="flex flex-wrap gap-2">
                {product.featured && (
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-yellow-100 text-yellow-800">
                    Featured
                  </span>
                )}
                {!product.published && (
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-red-100 text-red-800">
                    Unpublished
                  </span>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Content Sections */}
      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
        {/* Quote/Testimonial Section - Timeline-style Layout */}
        {product.quote && (
          <div className="mb-32 flex flex-col items-center">
            {/* Top vertical line - above quote */}
            <div className="w-px h-32 bg-stone-300 mb-16"></div>
            
            <div className="max-w-4xl mx-auto px-8 text-center">
              <blockquote>
                {/* Main quote - clean and severe */}
                <div className="text-2xl lg:text-3xl xl:text-4xl font-light text-stone-900 leading-[1.4] tracking-wide mb-12">
                  {product.quote}
                </div>
              </blockquote>
              
              {/* Attribution - minimal and refined */}
              {product.quote_attribution && (
                <div className="mt-16">
                  <div className="text-sm text-stone-500 font-light tracking-[0.3em] uppercase">
                    — {product.quote_attribution}
                  </div>
                </div>
              )}
            </div>
            
            {/* Bottom vertical line - below quote */}
            <div className="w-px h-32 bg-stone-300 mt-16"></div>
          </div>
        )}

        {/* Tabbed Content - only show if there are tabs with content */}
        {hasTabContent(product) && (
          <div className="mb-32">
            <ProductTabs product={product} />
          </div>
        )}
      </div>

      {/* Pairs Well With Products */}
      {pairsWellWithProducts.length > 0 && (
        <div className="bg-warm-beige py-12 pb-20">
          <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="mb-12">
              <div className="flex items-center">
                <div className="flex-grow border-t border-stone-300"></div>
                <div className="mx-6 bg-warm-beige px-4">
                  <span className="text-lg font-medium text-stone-600 tracking-wide uppercase">Good synergies with {product.manufacturer?.name}'s <span className="font-bold">{product.title}</span></span>
                </div>
                <div className="flex-grow border-t border-stone-300"></div>
              </div>
            </div>
          
            {relatedLoading ? (
              <div className="text-center py-16">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-stone-600 mx-auto mb-4"></div>
                <p className="text-stone-600">{getRandomMusicalMessage()}</p>
              </div>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-x-6 gap-y-10">
                {pairsWellWithProducts.map((relatedProduct: Product) => (
                  <Link key={relatedProduct.id} to={`/products/${relatedProduct.slug}`} className="block h-full">
                    <ProductCard
                      product={{
                        id: relatedProduct.id,
                        title: relatedProduct.title,
                        price: relatedProduct.price,
                        manufacturer: relatedProduct.manufacturer,
                        product_hero_image: relatedProduct.product_hero_image,
                        categories: relatedProduct.categories,
                        featured: relatedProduct.featured
                      }}
                      showCategory={false}
                    />
                  </Link>
                ))}
              </div>
            )}
          </div>
        </div>
      )}

      {/* Also Consider Products */}
      {alsoConsiderProducts.length > 0 && (
        <div className="bg-warm-beige py-12 pb-20">
          <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="mb-12">
              <div className="flex items-center">
                <div className="flex-grow border-t border-stone-300"></div>
                <div className="mx-6 bg-warm-beige px-4">
                  <span className="text-lg font-medium text-stone-600 tracking-wide uppercase">Also consider</span>
                </div>
                <div className="flex-grow border-t border-stone-300"></div>
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-x-6 gap-y-10">
              {alsoConsiderProducts.map((relatedProduct: Product) => (
                <Link key={relatedProduct.id} to={`/products/${relatedProduct.slug}`} className="block h-full">
                  <ProductCard
                    product={{
                      id: relatedProduct.id,
                      title: relatedProduct.title,
                      price: relatedProduct.price,
                      manufacturer: relatedProduct.manufacturer,
                      product_hero_image: relatedProduct.product_hero_image,
                      categories: relatedProduct.categories,
                      featured: relatedProduct.featured
                    }}
                    showCategory={false}
                  />
                </Link>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Image Modal */}
      {isImageModalOpen && selectedImage && (
        <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50" onClick={closeImageModal}>
          <div className="relative max-w-4xl max-h-full p-4">
            <img
              src={getImageUrl(selectedImage)}
              alt={product.title}
              className="max-w-full max-h-full object-contain"
            />
            <button
              onClick={closeImageModal}
              className="absolute top-4 right-4 text-white hover:text-gray-300"
            >
              <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
        </div>
      )}

      {/* Contextual CMS */}
      <ContextualCMS
        isOpen={isCMSOpen}
        onClose={() => setIsCMSOpen(false)}
        title={`Edit ${product?.title || 'Product'}`}
        context="product"
      >
        {product && (
          <ProductEditForm
            product={product}
            onSave={handleEditSuccess}
            onCancel={() => setIsCMSOpen(false)}
          />
        )}
      </ContextualCMS>

      {/* Delete Confirmation Modal */}
      <DeleteConfirmation
        isOpen={isDeleteModalOpen}
        onClose={() => setIsDeleteModalOpen(false)}
        onConfirm={handleDelete}
        title="Delete Product"
        message={`Are you sure you want to delete "${product.title}"? This action cannot be undone.`}
        loading={deleteLoading}
      />
    </div>
  );
};